*{margin:0;padding:0}html{box-sizing:border-box;overflow-x:hidden;font-family:"Open Sans",helvetica,arial,sans-serif;font-size:14px;background-color:#f8f8f8;height:100%}*,*:before,*:after{box-sizing:inherit}body{font-size:12px;font-family:"Open Sans",helvetica,arial,sans-serif;height:100%}main{max-width:1400px;margin:0 auto}img{max-width:100%}h1,h2,h3,h4,h5,h6{font-family:"Noto Serif",georgia,serif}

.Button{border:none;outline:none;text-decoration:none;font-family:"Open Sans",helvetica,arial,sans-serif;color:#1b1b1b;background:#f1f1f1;padding:4px 20px;text-decoration:none;border:1px solid #cacaca;font-size:12px}.Button:hover{background:#e2e2e2;text-decoration:none}.Button:active{background:#cfcfcf}

.SearchResult__button{border:none;outline:none;text-decoration:none;border-bottom:1px solid #e4e4e4;width:100%;font-family:"Open Sans",helvetica,arial,sans-serif;color:#1b1b1b;background:#f1f1f1;padding:5px;font-size:12px}.SearchResult__button:hover{background:#e4e4e4;text-decoration:none}

.SearchResults{border:1px solid #cacaca;min-width:200px;background-color:#fdfdfd;position:relative}.SearchResults__container{position:absolute;height:100%;overflow-y:auto;width:100%}.SearchResults__list{list-style-type:none}

.InputSearch{display:flex;flex-direction:rogw;background-color:#f7f7f7}.InputSearch__input{outline:none;border:none;border-left:1px solid #cacaca;border-right:1px solid #cacaca;padding:4px;display:block;flex-grow:1;background-color:#fdfdfd}.InputSearch__label{padding-left:8px;padding-right:8px;display:flex;align-items:center;color:#1b1b1b}.InputSearch__search{display:flex;flex-direction:row;flex-grow:1}.InputSearch__clear{outline:none;border:none;font-family:"Open Sans",helvetica,arial,sans-serif;color:#1b1b1b;background:#f1f1f1;padding:4px 20px;text-decoration:none;font-size:12px}

.ImportButton__input{display:none}





.InputText{background-color:pink}.InputText__input{outline:none;border:none;display:flex;height:100%;border:1px solid #cacaca;padding:4px;color:#1b1b1b;background-color:#fdfdfd}

.Home{display:flex;flex-direction:row;justify-content:center;height:100%}.Home__content{box-shadow:0px 0px 10px #cfcfcf;background-color:#fdfdfd}.Home__top{display:flex;flex-direction:row;justify-content:space-between;background-color:#e4e4e4;border:1px solid #cacaca;border-bottom:none}.Home__center{display:flex;flex-direction:row}.Home__bottom{display:flex;flex-direction:row-reverse;background-color:#e4e4e4;border:1px solid #cacaca;border-top:none}.Home__main{display:flex;flex-direction:column;border:1px solid #cacaca}.Home__result{outline:none;resize:none;border:none;border-top:1px solid #cacaca;background-color:#fdfdfd;color:#1b1b1b;font-size:12px;padding:4px;font-family:"Open Sans",helvetica,arial,sans-serif}.Home__result::-webkit-input-placeholder{color:#818181}.Home__result:-ms-input-placeholder{color:#818181}.Home__result::-ms-input-placeholder{color:#818181}.Home__result::placeholder{color:#818181}





